import { mapState, mapMutations } from 'vuex'
export default {
  name: 'Header',
  props: {
    currentMenu: {
      type: String,
      default: '自选区',
    },
  },
  data () {
    return {
      activeMenu: '',
    }
  },
  computed: {
    ...mapState(['locale', 'UserInfo', 'token']),
  },
  mounted () {
    this.$nextTick(() => {
      this.activeMenu = this.currentMenu
    })
  },
  methods: {
    settingPay () {
      this.$store.commit('SET_MENU', 'user')
      this.$router.push(`/${this.locale}/user/payment`)
    },
  },
}
